<template>
  <div class="pos">
    <el-row>
      <el-col :span='7' class="pos-order" id="order">
        <el-tabs>
          <el-tab-pane label="点餐">
            <el-table :data="tableData" style="width:100%">
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="count" label="量" width="50px"></el-table-column>
              <el-table-column prop="price" label="金额" width="70px"></el-table-column>
              <el-table-column label="操作" width="100" fixed="right">
                <template scope="scope">
                  <el-button type="text" size="small" @click="deleteSingle(scope.row)">删除</el-button>
                  <el-button type="text" size="small" @click="addOrder(scope.row)">增加</el-button>
                </template>
              </el-table-column>
            </el-table>
            <div class="prom">
              数量: {{totalCount}}; 金额:{{totalMoney}} ;
            </div>
            <div class="div-btn">
              <el-button type="warning">挂单</el-button>
              <el-button type="danger" @click="delAllgoods()">删除</el-button>
              <el-button type="success" @click="checkOut">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">
            挂单
          </el-tab-pane>
          <el-tab-pane label="外卖">
            外卖
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="17">
        <div class="often-goods">
          <div class="title">常用商品</div>
          <div class="often-goods-list">
            <ul>
              <li v-for=" goods in oftenGoods" @click="addOrder(goods)">
                <span>{{goods.goodsName}}</span>
                <span class="o-price">￥{{goods.price}}元</span>
              </li>
            </ul>
          </div>
        </div>
  
        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <div>
                <ul class='cookList'>
                  <li v-for="goods in type0Goods" @click="addOrder(goods)">
                    <span class="foodImg">
                      <img :src="goods.goodsImg" width="50%">
                    </span>
                    <p class="foodName" style="margin:0;text-align:center">{{goods.goodsName}}</p>
                    <p class="foodPrice" style="margin:0;text-align:center">￥{{goods.price}}元</p>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <ul class='cookList'>
                <li v-for="goods in type1Goods" @click="addOrder(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" width="50%">
                  </span>
                  <p class="foodName" style="margin:0;text-align:center">{{goods.goodsName}}</p>
                  <p class="foodPrice" style="margin:0;text-align:center">￥{{goods.price}}元</p>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <ul class='cookList'>
                <li v-for="goods in type2Goods" @click="addOrder(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" width="50%">
                  </span>
                  <p class="foodName" style="margin:0;text-align:center">{{goods.goodsName}}</p>
                  <p class="foodPrice" style="margin:0;text-align:center">￥{{goods.price}}元</p>
                </li>
              </ul>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <ul class='cookList'>
                <li v-for="goods in type3Goods" @click="addOrder(goods)">
                  <span class="foodImg">
                    <img :src="goods.goodsImg" width="50%">
                  </span>
                  <p class="foodName" style="margin:0;text-align:center">{{goods.goodsName}}</p>
                  <p class="foodPrice" style="margin:0;text-align:center">￥{{goods.price}}元</p>
                </li>
              </ul>
            </el-tab-pane>
          </el-tabs>
        </div>
  
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'pos',
  data() {
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: [],
      totalMoney: 0,
      totalCount: 0
    }
  },
  created: function () {
    axios.get('http://jspang.com/DemoApi/oftenGoods.php')
      .then(reponse => {
        // console.log(reponse)
        this.oftenGoods = reponse.data;
      })
      .catch(error => {
        alert('网络错误')
      })

    axios.get('http://jspang.com/DemoApi/typeGoods.php')
      .then(reponse => {
        //console.log(reponse)
        this.type0Goods = reponse.data[0];
        this.type1Goods = reponse.data[1];
        this.type2Goods = reponse.data[2];
        this.type3Goods = reponse.data[3];
      })
      .catch(error => {
        alert('网络错误')
      })

  },
  mounted: function () {
    var orderHeight = document.body.clientHeight;
    document.getElementById('order').style.height = orderHeight + 'px'
  },
  methods: {
    addOrder(goods) {

      this.totalMoney = 0;
      this.totalCount = 0;

      let isHave = false;
      for (let i = 0; i < this.tableData.length; i++) {

        if (this.tableData[i].goodsId == goods.goodsId) {

          isHave = true;
        }
      }

      if (isHave) {
        let arr = this.tableData.filter(o => o.goodsId == goods.goodsId);
        arr[0].count++
      } else {
        let newGoods = { goodsId: goods.goodsId, goodsName: goods.goodsName, goodsprice: goods.price, count: 1 };
        this.tableData.push(newGoods);
      }

      this.getAll();

    },
    deleteSingle: function (goods) {
      this.tableData = this.tableData.filter(o => o.goodsId != goods.goodsId)
      
      this.getAll();
    },
    delAllgoods(){
        this.tableData=[];
        this.totalCount=0;
        this.totalMoney=0;
    },
    checkOut(){
      if(this.totalCount!==0){
        this.tableData=[];
        this.totalCount=0;
        this.totalMoney=0;
        this.$message({
          message:'结账成功',
          type:'success'
        })
      }else{
        this.$message.error('没有商品')
      }
    },
    getAll() {
      this.totalCount = 0;
      this.totalMoney = 0;
      if (this.tableData) {
          this.tableData.forEach((element) => {
          this.totalCount += element.count;
          this.totalMoney = this.totalMoney + (element.goodsprice * element.count)
        })
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.pos-order {
  background-color: #F9FAFC;
  border-right: 1px solid #C0CCDA;
}

.div-btn {
  margin-top: 10px;
}

.title {
  height: 20px;
  border-bottom: 1px solid #D3dce6;
  background-color: #F9FAFC;
  padding: 10px;
  text-align: left
}

.often-goods-list ul li {
  list-style: none;
  float: left;
  border: 1px solid #E5E5E9;
  padding: 10px;
  margin: 10px;
  background: #FFF
}

.o-price {
  color: #5887FF
}

.goods-type {
  clear: both
}

.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #E5E9F2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
}

.cookList li span {

  display: block;
  float: left;
}

.foodImg {
  height: 100%;
  width: 100%;
}

.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}

.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
</style>



